.container-vine {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    item-align: center;
    background-color: white;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}
.containerIn {
    width: 1200px;
}
.img-container-vine {
    position:absolute;
    margin:3% 0 0 10%;
}
.caption-vine {
    width: 81px;
    height: 26px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.86;
    letter-spacing: 0.2px;
    color: #000000;
}
.vine-right-side {
    width:50%;
    margin-left:50%;
}
.vine-caption-item {
    width: 325px;
    height: 72px;
    font-size: 28px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.8px;
    color: #000000;
}
.vine-text {
    width: 518px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.86;
    letter-spacing: 0.2px;
    color: #5e5e5e;
