.inmodal {
    height: 95%;
    width: 90%;
    margin: 5%;
}

.bottom-side {
    margin-top: 150%;
    width:100%;
    height:100px;
}

.modal-button {
    width: 100%;
    margin-top:6%;
}

.marginR {
    margin-left: 70%;
}

.modal-img {
    position: absolute;
    margin-left: 6%;
}

.modal-counter {
    width:300px;
    height:130px;
    margin-left: 30%;
}

.Shanon {
    width: 193px;
    height: 42.2px;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.5px;
    color: #000000;
}

.close-item {
    position: absolute;
    margin: 0 20px 0 370px;
    cursor:pointer;
}

@-webkit-keyframes modal {
    0% {
        width: 0px;
    }
    100% {
        width:450px;
    }
}

@keyframes modal {
    0% {
        width: 0px;
    }
    100% {
        width:450px;
    }
}

.modal {
    -webkit-animation-name: modal;
            animation-name: modal;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    position: fixed;
    top: 0px;
    right: 0px;
    height:100%;
    width:450px;
    background-color: white;
}


